.select {
  position: relative;
  display: block;
  z-index: 1;
}
.select .select-head {
  width: 280px;
  height: 40px;
  border: 1px solid #666666;
  border-radius: 4px;
  padding: 8px;
  font-size: 20px;
  color: #3A4736;
  background-color: #fff;
  cursor: pointer;
}
.select .select-head svg {
  position: absolute;
  transform: translateY(50%);
  transition: 0.2s ease-in;
  right: 10px;
  bottom: 18px;
}
.select .select-head svg:hover {
  transform: translateY(50%) rotate(180deg);
}
.select .select-list {
  position: absolute;
  width: 100%;
  top: 120%;
  left: 0;
  right: 0;
  border-radius: 6px;
  z-index: 10;
  font-size: 20px;
  line-height: 20px;
  color: #3A4736;
  background-color: #fff;
  transition: 0.5s ease-in-out;
  transform: scaleY(0);
  transform-origin: 0 0;
}
.select .select-item {
  position: relative;
  padding: 8px;
  cursor: pointer;
  list-style-type: none;
}
.select .select-item:first-child {
  border-radius: 6px 6px 0 0;
}
.select .select-item:last-child {
  border-radius: 0 0 6px 6px;
}
.select .select-item:hover {
  background-color: #EEE4E2;
}
.select:hover .select-list {
  transform: scaleY(1);
}
.select:hover .select-head::after {
  transform: translateY(50%) rotate(180deg);
}

