@import url('slider/swiper-bundle.css')
@import url('authRegForm/reg.css')
@import url('authRegForm/auth.css')
@import url('greenbook.css')

$bg-main-color: #EDE1DE
$phone: 600px

@font-face
    font-family: 'Raleway'
    font-weight: 400
    src: url('../fonts/Raleway/static/Raleway-Regular.ttf')
@font-face
    font-family: 'Raleway'
    font-weight: 500
    src: url('../fonts/Raleway/static/Raleway-Medium.ttf')
@font-face
    font-family: 'Roboto'
    src: url('../fonts/Roboto/Roboto-Regular.ttf')

*,
*:before,
*:after
    margin: 0
    padding: 0
    box-sizing: border-box
    // font-weight: normal

ol,
ul
    list-style: none

a
    text-decoration: none

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration
    display: none

body
    font-family: 'Raleway',
    font-style: normal

body::-webkit-scrollbar
    width: 10px
    background-color: #f9f9fd

body::-webkit-scrollbar-thumb
    border-radius: 10px
    background-color: #87957E

body::-webkit-scrollbar-track
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2)
    border-radius: 10px
    background-color: #f9f9fd

button
    width: 250px
    height: 50px
    background: #87957E
    border: 1px solid #3A4736
    border-radius: 6px
    margin-top: 49px
    font-weight: 400
    font-size: 20px
    line-height: 20px
    color: #EEE4E2
    cursor: pointer
    position: relative
    z-index: 1
    overflow: hidden

button:hover
    color: #87957E

.title
    font-weight: 400
    font-size: 32px
    line-height: 20px
    color: #3A4736

.container
    max-width: 1210px
    margin: 0 auto

.header-main
    background-color: $bg-main-color
    min-width: 320px // 1140px ?
    position: sticky
    top: 0
    z-index: 100
    width: 100%
    // border: 1px solid #87957E
    // border-radius: 6px
    .header__container
        display: flex
        align-items: center
        justify-content: space-between

        .header__left-block
            display: flex
            align-items: center
            .left-block__main-logo
                background: url('../img/logo/Main-logo.svg') no-repeat
                height: 58px
                width: 111px
                margin-bottom: 6px
            // .main-logo__img
            //     width: 111px
            //     height: 51px
            //     padding: 3px 0px 0px

            .left-block__nav-head
                margin-left: 69px
                .nav-head__ul
                    display: flex
                    .current-page
                        background-color: rgba(81,80,81,.1490196078)
                        border-radius: 6px
                    .nav-head__li:nth-child(1)
                        margin-right: 40px
                    .nav-head__li:nth-child(2)
                        margin-right: 40px
                    .nav-head__li:nth-child(3)
                        margin-right: 40px
                    .nav-phone
                        display: none
                    .nav-head__btn
                        color: #3A4736
                        font-weight: 400
                        font-size: 20px
                        line-height: 20px
                        transition: all 0.2s ease-out
                        padding: 4px
                        // border-radius: 6px
                        &:hover
                            // box-shadow: 0 0 0 1px #87957E
                            color: #3a473678
        .header__right-block
            display: flex
            align-items: center

            .right-block__search-head
                display: flex
                align-items: center
                position: relative
                margin-right: 20px

                .search-head__input
                    border: none
                    border-radius: 6px
                    background-color: #51505126
                    width: 176px
                    height: 40px
                    padding: 8px
                    font-weight: 400
                    font-size: 16px
                    line-height: 24px
                    position: absolute
                    right: 77px
                    // z-index: 12
                    transition: 0.6s
                    color: #3a4736ec
                    letter-spacing: 0.01em
                    &::placeholder
                        font-weight: 400
                        font-size: 16px
                        line-height: 20px
                        color: #3a4736b9
                    &:focus
                        width: 210px
                        outline: none
                        right: 70px

                .search-head__btn
                    position: absolute
                    right: 87px
                    z-index: 1
                    .btn-head__img
                        fill: #3a4736b9
                        transition: all 0.1s ease
                        &:hover
                            fill: black

            .right-block__btn-head
                display: flex
                align-items: center
                position: relative
                .heart__btn
                    // margin-right: 22px
                    width: 32px
                    height: 31px
                    fill: none
                    stroke: #3A4736
                    transition: all 0.4s ease
                    position: absolute
                    right: 45px
                    border-radius: 6px
                    padding: 4px
                    &:hover
                        cursor: pointer
                        background: #87957e3a
                        border-radius: 6px
                        stroke: #f0e9e8

                .person__btn
                    width: 32px
                    height: 30px
                    fill: none
                    stroke: black
                    transition: all 0.4s ease
                    position: absolute
                    right: 0
                    stroke: #3A4736
                    border-radius: 6px
                    padding-left: 6px
                    &:hover
                        cursor: pointer
                        background: #87957e5c
                        border-radius: 6px
                        stroke: #EDE1DE
