.header-slider
    background-color: $bg-main-color
    .swiper
        padding-bottom: 40px
        width: 100%
        height: 100%
    .swiper-wrapper
        position: relative
        max-height: 100%
        .swiper-slide
            position: relative
        .swiper-slide__img
            width: 100%
            height: 500px
            object-fit: cover
        .container
            max-width: 1000px
            margin: 0 auto
            .slider__call
                position: absolute
                top: 0px
                .slide__text
                    font-weight: 400
                    font-size: 24px
                    line-height: 23px
                    color: #3A4736
                    margin-top: 18px
                .call__title
                    font-weight: 500
                    font-size: 40px
                    line-height: 45px
                    text-indent: 3px
                    color: #3A4736
                    margin-top: 80px
                .call__slide-2
                    margin-top: 77px
                button
                    width: 250px
                    height: 50px
                    background: #87957E
                    border: 1px solid #3A4736
                    border-radius: 6px
                    margin-top: 49px
                    font-weight: 400
                    font-size: 20px
                    line-height: 20px
                    color: #EEE4E2
                    cursor: pointer
                    position: relative
                    z-index: 1
                    overflow: hidden
                    transition: all 450ms ease-out
                button:hover
                    color: #87957E
                button::after
                    content: ""
                    background: #EEE4E2
                    position: absolute
                    z-index: -1
                    padding: 0.85em 0.75em
                    display: block
                button[class^="call"]::after
                    transition: all 450ms ease-out
                button[class^="call"]:hover::after
                    transition: all 450ms ease-out
                button.call__btn::after
                    border-radius: 50%
                    left: -30%
                    right: -30%
                    top: -150%
                    bottom: -150%
                    line-height: 8.34em
                    transform: scale(0, 0.4)
                    opacity: 0.5
                button.call__btn:hover::after
                    transform: scale(1, 1)
                    opacity: 1

    .swiper-pagination
        left: 0
        display: flex
        justify-content: center
        align-items: center
        .swiper-pagination-bullet
            background: #87957E
            width: 12px
            height: 12px
            border-radius: 85.8369px
        .swiper-pagination-bullet-active
            width: 53px !important
            height: 19px !important
            background: #87957E
            border: none
            border-radius: 85.8369px
