@media screen and (max-width: $phone)
    .container
        padding-left: 15px
        padding-right: 15px
    .interior
        padding-right: 15px
        .interior__title
            font-size: 15px
            font-weight: 700
            word-spacing: 0.2em
            margin-bottom: 30px
            padding-top: 40px
            line-height: 18px
            color: #3A4736
            position: relative
            &::before
                content: "Все"
                position: absolute
                right: 25px
                font-weight: 500
                font-size: 15px
                line-height: 18px
                color: #3A4736
                word-spacing: 0.2em
            &::after
                position: absolute
                right: 0
                content: url('../img/phone_logo/Arrow_phone.svg')

        .interior__cards
            grid-template-columns: 1fr 1fr
            grid-template-rows: 180px 180px
            gap: 12px 20px
            grid-template-areas: "Area2 Area3" "Area4 Area3"
            .img-div
                .interior__img
                    border-radius: 6px
                    filter: drop-shadow(5px 5px 8px rgba(0, 0, 0, 0.25))
                    border: none

            .interior__card:nth-child(1)
                display: none
            .interior__card:nth-child(2)
                margin-bottom: 0
                div:nth-child(2)
                    display: none
                div::after
                    display: none
            .interior__card:nth-child(3)
                margin-bottom: 0
                position: relative
                border-radius: 6px
                div:nth-child(2)
                    display: none
                div::after
                    display: none
                &::before
                    content: ""
                    display: block
                    background: linear-gradient(359.69deg, rgba(212, 172, 162, 0.48) 2.75%, rgba(255, 255, 255, 0.66) 48.51%)
                    z-index: 1
                    position: absolute
                    width: 100%
                    height: 100%
                    border-radius: 6px
                &::after
                    display: block
                    content: "Узнайте какие растения есть в этом интерьере"
                    position: absolute
                    bottom: 65px
                    font-weight: 500
                    font-size: 12px
                    line-height: 12px
                    width: 108px
                    right: 0
                    left: 0
